import React from 'react'

function ContactForm(props) {
    return (
        <form className="contact-form" action="https://contact.georgepestell.com/" method="POST">
            { props.error ? (<div className="alert error">Error</div>) : ""}
            { props.success ? (<div className="alert">SUCCESS</div>) : ""}
            <label>
                Full-Name:
                <input name="contact_fullname" />
            </label>
            <label>
                Email:
                <input name="contact_email" />
            </label>
            <label>
                Subject:
                <input name="contact_subject" />
            </label>
            <label>
                Comments:
                <textarea name="contact_content" />
            </label>
            <button type="submit">Submit</button>
        </form>
    )
}

export default ContactForm